import React, { useState, useRef } from 'react';
import { QRCode } from 'react-qrcode-logo';
import QrCodeLink from 'qrcode';
import './App.css';

// Importa a logo localmente
import logoImage from './logo.png'; // Substitua pelo nome correto do arquivo

function App() {
  const [link, setLink] = useState('');
  const [qrCodeLink, setQrCodeLink] = useState('');
  const qrCodeRef = useRef(null);

  function handleQRCode(event) {
    setLink(event.target.value);
    handleGenerate(event.target.value);
  }

  function handleGenerate(Link_url) {
    QrCodeLink.toDataURL(Link_url, {
      width: 600,
      margin: 5,
    }, function (err, url) {
      setQrCodeLink(url);
    });
  }

  function downloadQRCode() {
    const qrCodeCanvas = qrCodeRef.current.querySelector('canvas');
    const link = document.createElement('a');
    link.href = qrCodeCanvas.toDataURL('image/png');
    link.download = 'qrcode.png';
    link.click();
  }

  return (
    <div className='container'>
      <h1>Gerador de QR Code</h1>
      <div ref={qrCodeRef}>
        <QRCode
          className='qrcode'
          value={link}
          size={256}
          logoImage={logoImage} // Usando a imagem local
          logoWidth={62}
          logoHeight={62}
        />
      </div>
      <input
        className='input'
        placeholder='Digite seu link aqui'
        value={link}
        onChange={handleQRCode}
      />
      <button className='btn' onClick={downloadQRCode}>Baixar aqui</button>
    </div>
  );
}

export default App;
